import * as React from 'react';
import { useNavigate, useParams, Link } from "react-router-dom";
import { useContext, useState, useEffect } from 'react';

import noteContext from './NoteContext';


import API from "./API";


import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

import Snackbar from '@mui/material/Snackbar'; import Autocomplete from '@mui/material/Autocomplete';
import Form from 'react-bootstrap/Form';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import DoneAllIcon from '@mui/icons-material/DoneAll';

export default function Homework() {




    const { sr } = useParams();


    const [message, setmessage] = useState("");

    const [homeworkhistory, setHomeworkHistory] = useState([]);
    const [masterSubjects, setMasterSubjects] = useState([]);
    const [masterHomeworks, setmasterHomeworks] = useState([]);



    const [homework, setHomework] = useState("");

    const [subject, setSubject] = useState("");

    const [open, setOpen] = useState(false);


    const data = useContext(noteContext);


    const [student, setStudent] = useState();



    const navigate = useNavigate();

    let token = data.token;



    const handlesubmit = async () => {


        var reqdata = {

            "sr": sr,
            "homework": homework,
            "subject": subject

        }

        const res = await API.post("setHomework", reqdata, {
            headers: {
                'content-type': 'text/json', Authorization: 'Bearer ' + token
            }
        });



        fetchAPIData(sr);
        const dt = res.data;

        setmessage(dt.message);


        setOpen(true);

        //    navigate("/");

    }
    const deletehomework = async (item) => {



        const res = await API.post("delete_homework ", item, {
            headers: {
                'content-type': 'text/json', Authorization: 'Bearer ' + token
            }
        });
        setHomeworkHistory(res.data);

    }

    const markComplete = async (item) => {

        item.status = "Completed";
        item.remark = ".";

        const res = await API.post("homework_mark_complete", item, {
            headers: {
                'content-type': 'text/json', Authorization: 'Bearer ' + token
            }
        });
        setHomeworkHistory(res.data);

    }

    const fetchAPIData = async (sr) => {

        try {
            const res = await API.get("getHomework/" + sr, {
                headers: {
                    'content-type': 'text/json', Authorization: 'Bearer ' + token
                }
            });
            setHomeworkHistory(res.data);


            const res1 = await API.get("student/" + sr, {
                headers: {
                    'content-type': 'text/json', Authorization: 'Bearer ' + token
                }
            });


            setStudent(res1.data);

        }
        catch (error) {

            console.log(error);


        }


    };




    const fetchSubjects = async (sr) => {

        try {
            const res = await API.get("master/Subjects/" + sr, {
                headers: {
                    'content-type': 'text/json', Authorization: 'Bearer ' + token
                }
            });
            setMasterSubjects(res.data);




        }
        catch (error) {

            console.log(error);


        }


    };

    const fetchHomeWorks = async (sr) => {

        try {
            const res = await API.get("master/Homeworks/" + sr, {
                headers: {
                    'content-type': 'text/json', Authorization: 'Bearer ' + token
                }
            });


            setmasterHomeworks(res.data);



        }
        catch (error) {

            console.log(error);


        }


    };

    useEffect(() => {


        fetchAPIData(sr);
        fetchSubjects(sr);
        fetchHomeWorks(sr);



    }, []);



    if (student == undefined) return "";




    return (

        <>
            <strong>

                [ {student.sr} ]    {student.name} s/o   {student.father_name}
            </strong>




            <hr />











            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                >
                    <Typography>New Homework</Typography>
                </AccordionSummary>
                <AccordionDetails>

                    <div className="mb-3">

                        <Form.Select onChange={(e) => setSubject(e.target.value)} >
                            {masterSubjects.map((e) => <option value={e} key={e}>{e}</option>)}
                        </Form.Select>





                    </div>

                    <div className="mb-3">


                        <Form.Label>HomeWork</Form.Label>


                        <Form.Select onChange={(e) => setHomework(e.target.value)} >
                            {masterHomeworks.map((e) => <option value={e.id} key={e.id}>{e.homework}</option>)}
                        </Form.Select>



                    </div>






                    <div className="mb-3">

                        <Button onClick={handlesubmit} size="large" variant="contained">Set Homework</Button>

                        <Snackbar
                            open={open}
                            autoHideDuration={2000}
                            onClose={() => setOpen(false)}
                            message={message}

                        />
                    </div>





                </AccordionDetails>
            </Accordion>



            <Accordion>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel2a-content"
                    id="panel2a-header"
                >
                    <Typography>Past Homework</Typography>
                </AccordionSummary>
                <AccordionDetails>


                    <table className="table  table-auto  ">

                        <thead>


                            <tr>

                                <th>   Subject </th>
                                <th>  Homework  </th>
                                <th>  Start Date  </th>
                                <th> Create Date  </th>
                                <th>  Status </th>
                                <th> Mark complete  </th>

                                <th> Delete  </th>


                            </tr>
                        </thead>
                        <tbody>

                            {homeworkhistory.map((item, index) => (


                                <tr key={index}>



                                    <td>   {item.subject} </td>

                                    <td>   {item.homework} </td>

                                    <td>   {item.created_at} </td>

                                    <td>    {item.status === "Completed" && <>{item.updated_at}</>}
                                        .</td>

                                    <td>   {item.status} </td>

                                    <td>      <Button onClick={() => markComplete(item)} variant="contained"> <DoneAllIcon />    </Button>
                                    </td>

                                    <td>   <Button onClick={() => deletehomework(item)} variant="contained"> <DeleteForeverIcon /></Button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>



                </AccordionDetails>
            </Accordion>



        </>

    )
}